.dropdown-container {
  margin-bottom: 15px;
  margin-top: 15px;
}

.custom-dropdown {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  color: #333; /* Text color */
  width: 150px;
}

label {
  display: inline-block;
  margin-right: 5px;
  font-size: large;
  font-weight: bold;
}


.custom-dropdown:hover {
  background-color: #f9f9f9;
}

.custom-dropdown:focus {
  border-color: #007bff;
}

.custom-dropdown option {
  padding: 10px;
}
